import Vue from 'vue';
import VueRouter from 'vue-router'
// import Home from '../components/RouterPage/Home'
import AdminBoard from '../components/RouterPage/AdminPage'
import Airbnd from '../components/RouterPage/Airbnd.vue'
import Login from '../Views/login.vue'
import ClientDashboard from '../components/ClientDashboard.vue'
import Introduction from '../Views/Introduction.vue'
import SmartHome from '../components/SmartHome/Temperature-Hudimity.vue'

Vue.use(VueRouter);

const routes = [
    {
        path: '/Login',
        name: 'Login',
        component: Login,
        meta: {
            keepAlive: true,
            title: 'Login'
        }
    }, {
        path: '/Dashboard/:token',
        name: 'dashboard',
        component: ClientDashboard,
        meta: {
            keepAlive: true,
            title: 'Dashboard'
        }
    },
    {
        path: '/SmartHome/:token',
        name: 'SmartHome',
        component: SmartHome,
        meta: {
            keepAlive: true,
            title: 'SmartHome'
        }
    }
    ,
    {
        path: '/',
        name: 'Home',
        component: Introduction,
        meta: {
            keepAlive: true,
            title: 'Home'
        }
    },
    {
        path: '/AdminBoard',
        name: 'AdminBoard',
        component: AdminBoard,
        meta: {
            keepAlive: true,
            title: 'AdminBoard'
        }
    },
    {
        path: '/Airbnd',
        name: 'Airbnd',
        component: Airbnd,
        meta: {
            keepAlive: true,
            title: 'Airbnd'
        }
    },
]

const router = new VueRouter({
    routes
})

export default router
