<template>
  <div class="temperature-humidity-viiew">
    <div class="ubanner">
      <!-- <img src="@/assets/images/SmartHome/AK5678/AK-Frog.gif" alt="frog with yellow skin." height="150"> -->
      <img src="@/assets/images/SmartHome/AK5678/AK-Frog2.png" alt="frog with yellow skin." height="50">
    </div>
    <div class="content-container">
      <h1>FROG</h1>
      <!-- <p>user:{{userToken}}</p> -->
      <div class="sensor-data">
        <div>
          <h3>溫度</h3>
        </div>
        <div>
          <el-progress type="dashboard" :percentage="deviceData.temperatuer" :color="colors"></el-progress>
        </div>
      </div>
      <div class="sensor-data">
        <div>
          <h3> 濕度 </h3>
        </div>
        <div>
          <el-progress type="dashboard" :percentage="deviceData.humidity" :color="colors"></el-progress>
        </div>
      </div>

      <div class="sensor-data fan-view">
        <h3>風扇運轉狀態</h3>
        <div v-if="deviceData.fanOpenState">
          <img src="@/assets/images/SmartHome/AK5678/fan-gif.gif" alt="" height="100">
        </div>

        <div v-else>
          <img src="@/assets/images/SmartHome/AK5678/fan-close.png" alt="" height="100">
        </div>

        <el-button @click="FanControl">開關</el-button>
      </div>

    </div>

    <!-- <div class="footer-animate">
      <div id="frog-jump-img">
        <img src="@/assets/images/SmartHome/AK5678/jumping-frog.png" alt="frog with yellow skin." height="50">
      </div>
    </div> -->
  </div>
</template>

<script>
// import gsap from "gsap";
import { BackendService } from "../../API/HttpCRUD";
export default {
  data() {
    return {
      userToken: 7527111,
      temperature: 28,
      colors: [
        { color: "blue", percentage: 25 },
        { color: "seagreen", percentage: 30 },
        { color: "orange", percentage: 33 },
        { color: "#1989fa", percentage: 80 },
        { color: "#6f7ad3", percentage: 100 },
      ],
      deviceData: {},
      fanControlParam: {
        OperatorMode: 0,
        FanOpen: false,
      },
    };
  },
  methods: {
    async fetchData() {
      var data = await BackendService.apiSmartHomeGetDeviceData();
      //   console.log(data);
      if (data.status != 200) {
        return;
      }
      this.deviceData = data.data;
    },
    async FanControl() {
      var data = await BackendService.apiSmartHomeFanControl(
        this.fanControlParam.OperatorMode,
        this.fanControlParam.FanOpen
      );
      if (data.status != 200) {
        return;
      }
      console.log(data);
    },
  },
  mounted() {
    this.userToken = this.$route.params.token;
    setInterval(async () => {
      await this.fetchData();
    }, 1000);
  },
};
</script>

<style scoped>
h1 {
  letter-spacing: 5px;
}

h3 {
  letter-spacing: 3px;
}
.temperature-humidity-viiew {
  background-color: rgb(28, 28, 28);
  color: gray;
  height: 100vh;
}

.ubanner {
  background-color: whitesmoke;
  color: black;
  height: auto;
  width: 100%;
}

.sensor-data {
  width: 100%;
  margin: auto;
}
/* 
.sensor-data img {
  width: 100%;
  height: 100%;
} */
@media screen and (max-width: 450px) {
  .sensor-data {
    width: 50%;
    margin: auto;
    float: left;
  }

  .fan-view {
    width: 100%;
  }
}

.footer-animate {
  background-color: whitesmoke;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 100px;
}

#frog-jump-img {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 50px;
  height: 50px;
  /* background-color: red; */
}
</style>
