<template>
  <transition name="fade">
    <div v-show="!change_route" class="introduction">
      <transition name="fade">
        <div v-if="bg_show" class="bg"> </div>
      </transition>
      <transition name="slideRight">
        <div v-if="banner_show" class="banner">
          <router-link to="/Contact" tag="button" class="big-button"><span>Contact</span></router-link>
          <router-link to="/Login" tag="button" class="big-button"><span>Sign Up</span></router-link>
          <router-link to="/Login" tag="button" class="big-button"><span>Sign In</span></router-link>
          <router-link to="/About" tag="button" class="big-button"><span>About</span></router-link>
        </div>
      </transition>

      <transition name="fade">
        <div v-if="info_1_show" class="intro">
          <h1>製作資訊看板原來這麼簡單</h1>
          <div class="descripton">
            <p>透過簡單的API呼叫，讓你的數據呈現網頁上讓全世界的人瀏覽</p>
          </div>
          <button class="big-button" id="getting-start-btn" @click="GoToLoginPage">GETTING START</button>
        </div>
      </transition>

      <transition name="fade">
        <div v-if="info_2_show" class="intro">
          <h1>造福了使用者</h1>
          <div class="descripton">
            <p>透過簡單的API呼叫，讓你的數據呈現網頁上讓全世界的人瀏覽</p>
          </div>
        </div>
      </transition>

      <div>
      </div>
    </div>
  </transition>

</template>

<script>
require("vue2-animate/dist/vue2-animate.min.css");

export default {
  data() {
    return {
      change_route: false,
      bg_show: false,
      banner_show: false,
      info_1_show: false,
      info_2_show: false,
    };
  },
  methods: {
    Start_Animation() {
      setTimeout(() => {
        this.bg_show = true;
      }, 700);
      setTimeout(() => {
        this.info_1_show = true;
      }, 1800);
      setTimeout(() => {
        this.info_2_show = true;
        this.banner_show = true;
      }, 2500);
    },
    GoToLoginPage() {
      this.change_route = true;
      setTimeout(() => {
        this.$router.push("/Login");
      }, 100);
    },
  },
  mounted() {
    this.Start_Animation();
  },
};
</script>

<style>
.introduction {
  height: 100vh;
  background-color: transparent;
  color: rgb(40, 44, 52);
  animation-duration: 800ms;
}
.introduction .bg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("../assets/images/landing-bg.png");
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  animation-duration: 2s;
}

.introduction h1 {
  letter-spacing: 3px;
}

.introduction .intro {
  float: left;
  /* opacity: 0.6; */
  color: white;
  margin: 0;
  /* background-color: teal; */
  text-align: center;
  margin: 200px;
  animation-duration: 1.5s;
}
.descripton p {
  color: white;
  width: 250px;
  white-space: nowrap;
  font-size: 20px;
  letter-spacing: 2px;
}

@media screen and (max-width: 500px) {
  .introduction h1 {
    font-size: 1.4em;
  }
  .descripton p {
    color: white;
    width: 100%;
    white-space: nowrap;
    font-size: 10px;
    letter-spacing: 2px;
    text-align: center;
  }

  #getting-start-btn {
    background-color: gold;
    color: white;
    font-size: 2px;
  }
}

.big-button {
  border: none;
  font-size: 2em;
  letter-spacing: 3px;
  border-radius: 0.4em;
  padding: 20px;
  cursor: pointer;
  margin: 5px;
}

#getting-start-btn {
  background-color: red;
  color: white;
  opacity: 0.84;
  font-size: 1em;
}

#getting-start-btn:hover {
  opacity: 1;
}

.banner {
  background-color: transparent;
  position: absolute /* background-color: black; */;
  width: 100%;
  animation-duration: 1.8s;
}
.banner .big-button {
  background-color: transparent;
  color: white;
  float: right;
  height: 20px;
  padding: 5px;
  margin: 20px;
  font-size: 1.1em;
  text-decoration-line: underline;
  transition: transform 1.5s;
  transform-style: preserve-3d;
}

.banner .big-button:hover {
  background-color: white;
  color: black;
  height: 35px;
  padding: 10px;
  text-decoration-line: none;
  transform: rotateY(360deg);
}

.banner .big-button span {
  display: inline-block;
  border-bottom: 1px solid white;
  padding-bottom: 3px;
}
</style>
