<template>
  <transition name="fade">
    <div v-show="pageshow" id="login">
      <div class="form">
        <div class="el-icon-user-solid" id="form-icon"></div>
        <div id="description">服務登入</div>
        <div id="ele-form">
          <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="180px" class="demo-dynamic">
            <el-form-item prop="id" label="Email" :rules="[
      { required: true, message: '請輸入 Email', trigger: 'blur' },
      { type: 'email', message: '請輸入正確的的 Email', trigger: ['blur', 'change'] }]">
              <el-input v-model="dynamicValidateForm.id" native.autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item prop="password" label="密碼" :rules="[
      { required: true, message: '請輸入密碼', trigger: 'blur' }]">
              <el-input v-model="dynamicValidateForm.password" show-password></el-input>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="submitForm('dynamicValidateForm','login')">登入</el-button>
              <el-button type="danger" @click="Regist('userRegistValidateForm','regist')">註冊</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
require("vue2-animate/dist/vue2-animate.min.css");
import { BackendService } from "../API/HttpCRUD";
export default {
  data() {
    return {
      pageshow: false,
      Action: "",
      dynamicValidateForm: {
        id: "",
        password: "",
      },
      userRegistValidateForm: {
        id: "123@gmail.com",
        password: "55688",
      },
    };
  },
  computed: {},
  methods: {
    async submitForm(formName, action) {
      console.log("action:", action);
      this.$refs[formName].validate(async (valid) => {
        // if (process.env.NODE_ENV != "production") {
        //   this.EnterDashboard("testtoken23v=" + Date.now());
        //   return;
        // }
        if (valid) {
          let ret = await BackendService.apiDashboardServiceUserLogin(
            this.dynamicValidateForm
          );
          console.log(ret.data);
          if (!ret.data.verifyPass) {
            if (ret.data.networkError) {
              alert("[網路異常]資料庫連接失敗");
            } else if (!ret.data.accountExist) {
              alert("帳戶不存在!");
            } else if (!ret.data.passwordCorrect) alert("密碼錯誤!");
          } else {
            alert("Welcome! " + this.dynamicValidateForm.id + " !");
            this.EnterDashboard(ret.data.token);
          }
        } else {
          this.$router.replace("/Dashboard");
          return false;
        }
      });
    },
    async Regist() {
      var res = await BackendService.apiDashboardServiceUserRegist(
        this.userRegistValidateForm
      );
      this.EnterDashboard(res.data.token);
    },
    EnterDashboard(token) {
      this.$router.push({
        name: "dashboard",
        path: "/Dashboard/" + token,
        params: { token: token, father: true },
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  mounted() {
    this.pageshow = true;
  },
};
</script>

<style>
#login {
  background-color: white;
  height: 100vh;
  color: black;
  animation-duration: 1000ms;
}
@media screen and (max-width: 500px) {
  #login {
    background-color: white;
    height: 100vh;
    width: fit-content;
    color: black;
  }
}

.form {
  width: 500px;
  height: 300px;
  margin: auto;
  border: none;
  border-radius: 50px;
  background-color: white;
  position: relative;
  top: 213px;
  color: black;
  box-shadow: 0.2em 0.2em 2.4em 0.2em black;
  /* color: white; */
}
#description {
  font-size: 30px;
  position: relative;
  top: -30px;
}
#form-icon {
  position: relative;
  top: -40px;
  font-size: 6em;
  color: rgba(53, 53, 53, 0.8);
  border-radius: 4em;
  background-color: white;
}
#ele-form {
  position: relative;
  top: -20px;
  right: 78px;
  color: black;
  /* background-color: green; */
}
.el-form-item__label {
  color: white;
}
</style>
