<script>
export default {
  data() {
    return {
      search_btn_text: "",
      render: -1,
    };
  },
  methods: {
    searchBtnClick() {
      this.search_btn_text = "搜尋";
    },
    HeaderSticky() {
      var header = document.getElementById("banner");
      var sticky = header.offsetTop;
      if (window.pageYOffset > sticky) {
        header.classList.add("sticky");
      } else {
        header.classList.remove("sticky");
      }
    },
  },
  computed: {
    IsUseCircle() {
      return this.search_btn_text == "";
    },
  },
  mounted() {
    document.onclick = function () {
      console.log("click any ");
      this.search_btn_text = "";
      this.render += 1;
    };
    document.onscroll = () => this.HeaderSticky();
  },
  beforeDestroy() {
    document.onscroll -= this.HeaderSticky();
  },
};
</script>

