import axios from 'axios';

axios.interceptors.request.use(config => {
    config.withCredentials = true;
    return config;
});

const backendServiceRequest = axios.create({
    baseURL: process.env.NODE_ENV === 'production' ? window.location.protocol + "//" + window.location.host : 'http://localhost:6999'
});


/**後端服務 */
export class BackendService {
    /**取得線上使用者列表 */
    static apiGetUserList(){ backendServiceRequest.get('/api/v1/admin/userlist')
    }
    static  apiDashboardServiceUserLogin(data){
        return  backendServiceRequest.post('api/dashboard/login', data)
    }
    static apiDashboardServiceUserRegist(data){
        return  backendServiceRequest.post('api/dashboard/regist', data)
    }
    static apiDashboardServiceeUserDataFetch(token){
        return  backendServiceRequest.get(`api/dashboard/fetch_data/${token}`)
    }

    static async apiSmartHomeGetDeviceData(){
       return backendServiceRequest.get("api/SmartHome/FrontEnd/GetDeviceMeasureValue")
    }
    static async apiSmartHomeFanControl(mode,Switch){
        return backendServiceRequest.get(`api/SmartHome/FrontEnd/FanControl/${mode}/${Switch}`)
     }

}

//向外部API請求
export const CarFixQuery = {
    apiGetToyotaTaichung: () => {
        if (process.env.NODE_ENV === 'production') {
            axios.defaults.headers = { 'Access-Control-Allow-Origin': 'true' }
            axios.defaults.baseURL = "http://www.toyotacm.com.tw/toyota/location.php"
            //TODO 跨域請求總是失敗 
        }
        return axios.get("/toyota?city=1&country=", { crossdomain: true });
    },
}

